.page-header {
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
  flex-direction: column;
  display: flex;
  align-items: center;

  @include bg-img("../img/hero@1x.jpg");


  @media (min-resolution: $retina-dppx) {
    background-image: url("../img/hero@2x.jpg");
    background-size: 1440px auto;
    // width: 1440px;
    height: 680px;
  }
  
  .wrapper {
    @include wrapper(column, nowrap, space-between, center, 940px, 100%);
  
    .page-header__navigation .side:nth-child(3) {
      width: 65%;
    }
    .navigation {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      width: 100%;
      padding-top: 28px;
      align-items: baseline;
      
      .side {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        width: 100%;
        padding: 0;
        list-style-type: none;
        // @include font(16px, 500, 19px, $text-light);
        
        &__item {
          // color: $text-light;
          // @include font(16px, 500, 19px, $text-light);
        }
        &__link {
          @include font(16px, 500, 19px, $text-light, center);
          // color: #fff;
        }
      }
      
      &__logo {
        margin: 0 89px 0 98px;
        align-self: center;
      }
      &__accent-btn{
        @include button(15px, 15px);
        margin-left: 38px;
        display: flex;
        height: 32px;

        .icon-white-heart {
          margin-left: 9px;
        }       
      }
    }
  }

  .page-header__block-hero,
  .block-hero {
    margin-top: 198px;

    .wrapper {
      @include wrapper(column, nowrap, space-between, center, 940px, 100%);

      &__head {
        margin-top: 198px;
        @include font(36px, 700, 40px, $text-light, center);

      }
    }
  }
}

